/**
 * //  n  len 
 * @param {Array} arr 
 * @param {Number} len 
 */
const split_array = (arr, len) => {
    var a_len = arr.length;
    var result = [];
    for (var i = 0; i < a_len; i += len) {
        result.push(arr.slice(i, i + len));
    }
    return result;
}

export default split_array


