/**
 *   
 * @param {*} num   
 * @param {*} f 	
 */
const money_format = (num, f) => {
    num = String(num) //

    let Old_f = null
    if (num.search(/\./) > -1) {
        Old_f = num.toString().split(".")[1].length //
    }
    if (Old_f && Old_f !== 'null' && Old_f < f) { // 0
        for (let i = 0; i < (f - Old_f); i++) {
            num += '0'
        }
    }
    num = Math.floor(Number(num).toFixed(f) * (Math.pow(10, f))) + "" //tofixxed 

    let integer = num.slice(0, num.length - f) + "" //
    let decimal = num.slice(num.length - f, num.length) + "" //

    let thou = Math.floor(integer.length / 3) //thousandCentimeter 
    if (integer.length % 3 == 0) {
        thou = thou - 1 //
    }
    let new_integer = '' //
    let temp = 0 //
    for (let i = 0; i < thou + 1; i++) {
        let target = integer.length - (thou - i) * 3 //thou-1  54321  
        new_integer += integer.slice(temp, target) //
        if (i !== thou) {
            new_integer += ',' //
        }
        temp = target
    }
    integer = new_integer

    return {
        full: integer + '.' + decimal, //
        arr: [integer, decimal] //    
    }
}

export default money_format


